body {
  background-color: rgb(220, 220, 220);
}

.calculator {
  max-width: 200px;
  margin: 100px auto;
  border: 2px solid #ccc;
  border-radius: 5px;
  padding: 20px;
  background-color: #333129;
  border-radius: 20px;
}

.display {
  font-size: 24px;
  text-align: right;
  height: 40px;
  margin-bottom: 30px;
  padding: 5px;
  border: 1px solid #ccc;
  background-color: #ffffff;
  border-radius: 10px;
}

.buttons {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 10px;
}

button {
  font-size: 20px;
  border: none;
  background: #ccc;
  cursor: pointer;
  border-radius: 50px;
  transition: background 0.1s ease;
}

button:hover {
  background: #f0f0f0; /* Light gray color on hover */
}

button:focus {
  outline: none; /* Remove focus outline on buttons */
}

.number-button {
  font-size: 20px;
  padding: 10px;
  border: none;
  background: #767e9a;
  cursor: pointer;
}

.number-button:hover {
  background: #8a8e9b; /* Light grey color on hover for number buttons */
}

.operator-button {
  font-size: 20px;
  padding: 10px;
  border: none;
  background: #dfc440;
  cursor: pointer;
}

.operator-button:hover {
  background: #d9cc8e; /* Light yellow color on hover for operator buttons */
}

.button-pressed {
  background: #ccc; /* Light gray color for pressed state */
}

